<template>
  <div class="card" @click="flip">
    <div class="face" :style="{ backgroundImage: `url(${face})` }"></div>
    <div class="back" :style="{ backgroundImage: `url(${back})` }"></div>
  </div>
</template>

<script>
export default {
  name: 'CardView',
  props: {
    face: String,
    back: String,
  },
  methods: {
    flip() {
      this.$el.classList.toggle('flipped');
    },
  },
};
</script>
<style scoped>
.card {
  position: relative;
  width: 25vw;
  padding-bottom: 40vw;
  perspective: 400px;
  perspective-origin: 40% 50%;
  /* 添加透视中心 */
  border-radius: 12px;
  overflow: hidden;
  transform-style: preserve-3d;
  /* 添加 3D 空间 */
  margin: 20px;
}

.face,
.back {
  position: absolute;
  top: 0;
  left: 0;
  width: 90%;
  height: 80%;
  background-size: cover;
  background-position: center;
  backface-visibility: hidden;
  transition: transform 0.8s;
  border-radius: 12px;
  will-change: transform;
  /* 添加性能优化 */
}

.face {
  /* background-image: url('https://jsd.cdn.zzko.cn/gh/mlgbz523/imgURL/src/wallhaven-1pwxv1_1920x1536.png'); */
  transform: rotateY(0deg);
}

.back {
  /* background-image: url('https://jsd.cdn.zzko.cn/gh/mlgbz523/imgURL/src/1704126097411.jpg'); */
  transform: rotateY(180deg);
}

.card:hover .face {
  transform: rotateY(-180deg);
}

.card:hover .back {
  transform: rotateY(0deg);
}
</style>